@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&family=Walter+Turncoat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: rgb(255, 255, 255);
  width: 100%;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  font-family: 'Walter Turncoat', cursive;
}

.current-display {
  margin-top: 10vh;
  width: 100%;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
}

/* ------------------------------------------ Header ------------------------------------------ */

.header {
  background-color: rgb(251,251,251);
  height: 10vh;
  box-shadow: 0px 0px 3px 3px rgb(226, 226, 226);
  display: flex;
  overflow: hidden;
  position: fixed;
  align-items: center;
  width: 100vw;
  z-index: 3;
}

.header div {
  /* align-self: center; */
  transition: transform 300ms;
  transform-origin: center;
  margin-left: 3%;
  /* margin-top: 0.5vh */
}

.header div:hover {
  cursor: pointer;
}
 
#logo {
  margin-left: 4vw;
  margin-right: auto;
  margin-top: 2vh;
}

#logo img {
  height: 10vh;
  pointer-events: none;
}

.new-recipe {
  color: rgb(148, 148, 148);
}


#new-recipe-icon-container {
  margin-top: 1vh;
  width: 24px;
}


#new-recipe-icon-container i:hover {
  color: #C70017;
}

#new-recipe-icon-container i{
  position: absolute;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  color: rgb(148, 148, 148);
}


.home {
  color: rgb(148, 148, 148);
}

.home:hover {
  color: #C70017;
}

.log-in-out {
  display: flex;
  margin-right: 4vw;
}

/* -------------------------------------------------------------------------------------------- */

/* -------------------------------------- SIGNED-IN-USER -------------------------------------- */

.sign-in {
  color: rgb(148, 148, 148);
}

.sign-in:hover {
  color: #C70017;
}

/* .user-name {
  margin: auto;
} */

.user-pic {
  height: 7vh;
  width: 7vh;
  border-radius: 50%;
}

/* ------------------------------------------ Home Display ------------------------------------------ */


.home-view {
  width: 90vw;
  min-height: 70vh;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.more-recipes-container {
  display: flex;
  margin-top: 1vw;
  margin-bottom: 3vw;
  flex-wrap: wrap;
  cursor: pointer;
  width: 11vw;
  margin-left: auto;
  margin-right: auto;
}

.more-recipes-container:hover div {
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
  cursor: pointer;
}

.more-recipes-title {
  margin: auto;
  width: 100%;
  -webkit-animation-name: load;
          animation-name: load;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes load {
  0% { transform: scale(1) }
  50% { transform: scale(1.2)  }
  100% { transform: scale(1)  }
}

@keyframes load {
  0% { transform: scale(1) }
  50% { transform: scale(1.2)  }
  100% { transform: scale(1)  }
}

.more-recipes-icon {
  margin: auto;
  -webkit-animation-name: bob;
          animation-name: bob;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

.more-recipes-icon i {
  pointer-events: none;
}

@-webkit-keyframes bob {
  0% { transform: translateY(0px); }
  50% { transform: translateY(30px); }
  100% { transform: translateY(0px); }
}

@keyframes bob {
  0% { transform: translateY(0px); }
  50% { transform: translateY(30px); }
  100% { transform: translateY(0px); }
}


/* ------------------------------------------ Home View Recipe Cards ------------------------------------------ */



.recipe-card {
  background-color: transparent;
  min-width: 200px;
  width: 16vw;
  min-height: calc(200px * 1.22);
  height: calc(16vw * 1.22);
  perspective: 1000px;
  margin: 1vw;
}


/* This container is needed to position the front and back side */
.recipe-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  box-shadow: 0px 0px 13px 0px #8a8a8a;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.recipe-card:hover .recipe-card-inner {
  transform: rotateY(180deg);
}

/* Position the front and back side */
.recipe-card-front, .recipe-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.recipe-card-front {
  background-color: #ffffff;
  background-image: url("https://www.transparenttextures.com/patterns/cream-pixels.png");
  color: black;
  display: grid;
  /* grid-template-rows: 277px 59px; */
  grid-template-rows: 4.7fr 1fr;
  text-align: center;
  border-radius: 2px;
}

.image-container {
  margin-top: 6%;
  margin-bottom: auto;
  justify-self: center;
  width: 88%;
}

.image-container img {
  width: 100%;
  transform: translate3d(0,0,0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.recipe-front-text {
  grid-row-start: 2;
  grid-row-end: 3;
  align-self: end;
  font-size: .7vw;
  margin-bottom: 1vw;
}

.recipe-card-back {
  background-color: white;
  color: black;
  transform: rotateY(180deg);
  text-align: center;
  border-radius: 2px;
  display: grid;
  grid-template-rows: 1fr 1fr 4fr;
  overflow: hidden;
}

.recipe-back-title {
  grid-row-start: 1;
  grid-row-end: 2;
  font-size: .6vw;
  overflow: hidden;
}

.recipe-back-description {
  grid-row-start: 3;
  grid-row-end: 4;
  font-size: .9vw;
  width: 90%;
  margin: 0px auto;
  text-align: center;
  overflow: hidden;
  font-family: 'Walter Turncoat', cursive;
}

.recipe-back-author-container {
  grid-row-start: 2;
  grid-row-end: 3;
  display: grid;
  grid-template-rows: 1fr 1fr;
  position: relative;
}

.recipe-back-author {
  grid-row-start: 2;
  grid-row-end: 3;
  font-size: .7vw;
}

.recipe-back-author-pic img {
  min-width: 5vh;
  width: 3vw;
  min-height: 5vh;
  height: 3vw;
  border-radius: 50%;
  background-color: grey;
  grid-row-start: 1;
  grid-row-end: 2;
  position: absolute;
  left: 50%;
  bottom: 50%;
  transform: translate(-50%, 50%);
  transition: all 300ms;
  transform-origin: center;
}

.recipe-back-author-pic img:hover {
  width: 10vh;
  height: 10vh;
}


/* ------------------------------------------ Add Recipe Display ------------------------------------------ */

.new-recipe-view {
  height: 90vh;
  width: 100vw;
  display: grid;
  grid-template-columns: minmax(250px, 25vw) 1fr;
  position: relative;
}

.recipe-form {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  box-shadow: 0px 0px 2px 2px rgb(200, 200, 200);
  display: grid;
  grid-template-rows: 10vh 70vh 10vh;
  margin: auto;
  background-color: rgb(251,251,251);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.recipe-form label {
  /* margin: auto; */
  grid-row-start: 2;
  grid-row-end: 3;
  display: grid;
  font-size: 2.2vh;
}

.recipe-form h2 {
  font-style: italic;
}

.recipe-creation-header {
  grid-row-start: 1;
  grid-row-end: 2;
  color: #C70017;
  margin: auto;
  font-size: 1.8vh;
}

.form-title {
  grid-template-rows: 1fr 1fr 5fr;
}

.form-title input {
  margin: auto;
  all: unset;
  border-bottom: 2px solid rgba(0, 0, 0, 0.623);
  height: 4vh;
  width: 80%;
  margin: 0 auto 0 auto;
  /* margin-top: 4vh; */
}

.form-title input:focus {
  border-bottom: 2px solid green;
}

.form-photo {
  grid-template-rows: 1fr 1fr 1fr 4fr;
}

.form-photo input {
  all: unset;
  width: 75%;
  margin: auto;
}

.form-description {
  grid-template-rows: 1fr 1fr 5fr;
}

.form-description textarea {
  all: unset;
  border: 1px solid grey;
  background-color: white;
  height: 60%;
  width: 80%;
  margin-right: auto;
  margin-left: auto;
}

.form-button-container {
  grid-row-start: 3;
  grid-row-end: 4;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
}

.form-button-container i {
  margin: auto;
}

.form-button-container i:hover {
  cursor: pointer;
  color: rgb(0, 138, 18);
  transform: scale(1.1);
}

.previous-step-button {
  grid-column-start: 1;
  grid-column-end: 2;
  display: flex;
}

.next-step-button {
  grid-column-start: 3;
  grid-column-end: 4;
  display: flex;
}

.create-button-container {
  margin: auto;
  grid-row-start: 2;
  grid-row-end: 3;
  display: grid;
  grid-template-rows: 1fr 1fr 5fr;
  font-size: 2.1vh;
}

.submit-button {
  all: unset;
  align-self: center;
  margin: auto;
  color: white;
  font-style: italic;
  background-color: rgb(0, 163, 35);
  border: 2px solid black;
  box-shadow: 0px 0px 2px 2px grey;
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  margin-top: 5vh;
  position: relative;
}

.submit-button:hover {
  cursor: pointer;
}

.submit-button-inner {
  background-color: rgb(0, 163, 35);
  border: 2px solid black;
  box-shadow: 0px 0px 2px 2px white;
  height: 8vw;
  width: 8vw;
  border-radius: 50%;
  margin: auto;
}

@-webkit-keyframes pulse {
  0% {height: 10vw; width: 10vw;}
  100% {transform: translate(-50%, -50%) scale(1.4) ; border: 1px solid transparent}
}

@keyframes pulse {
  0% {height: 10vw; width: 10vw;}
  100% {transform: translate(-50%, -50%) scale(1.4) ; border: 1px solid transparent}
}

.flashers {
  height: 10vw;
  width: 10vw;
  border-radius: 50%;
  border: 1px solid green;
  background-color: transparent;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 900ms;
          animation-duration: 900ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  transform-origin: center;
}

.flasher-1 {
  -webkit-animation-delay: 0ms;
          animation-delay: 0ms;
}

.flasher-2 {
  -webkit-animation-delay: 100ms;
          animation-delay: 100ms;
}

.flasher-3 {
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms;
}

.submit-button:focus div {
  box-shadow: 0px 0px 2px 2px white inset;
}

.submit-button-fake {
  all: unset;
  align-self: center;
  margin: auto;
  color: white;
  font-style: italic;
  background-color: rgb(129, 129, 129);
  border: 2px solid black;
  box-shadow: 0px 0px 2px 2px grey;
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  margin-top: 5vh;
  pointer-events: none;
}

.submit-button-fake div {
  background-color: rgb(129, 129, 129);
  border: 2px solid black;
  box-shadow: 0px 0px 2px 2px white;
  height: 8vw;
  width: 8vw;
  border-radius: 50%;
  margin: auto;
}

.sign-in-arrow {
  position: absolute;
  right: 2vw;
  color: #C70017;
  -webkit-animation-name: point-up;
          animation-name: point-up;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  z-index: 10;
}

@-webkit-keyframes point-up {
  0% {transform: translateY(0%) scale(1.2);}
  50% {transform: translateY(100%);}
  100% {transform: translateY(0%) scale(1.2);}
}

@keyframes point-up {
  0% {transform: translateY(0%) scale(1.2);}
  50% {transform: translateY(100%);}
  100% {transform: translateY(0%) scale(1.2);}
}

.draft-recipe-card-container {
  grid-column-start: 2;
  grid-column-end: 3;
  display: flex;
  margin: auto;
}


/* ------------------------------------------------------- Add Recipe Draft Card --------------------------------------------------------------------------------------- */


.recipe-card-creation {
  background-color: transparent;
  min-width: 280px;
  width: 24vw;
  min-height: calc(280px * 1.22);
  height: calc(24vw * 1.22);
  perspective: 1000px;
}


/* This container is needed to position the front and back side */
.recipe-card-inner-creation {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  box-shadow: 0px 0px 13px 0px #8a8a8a;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.recipe-card-creation:hover .recipe-card-inner-creation {
  transform: rotateY(180deg);
}

.flip {
  transform: rotateY(180deg);
}

.spin {
  transform: rotateY(1080deg);
}

.spin:hover {
  transform: rotateY(1260deg);
}

/* Position the front and back side */
.recipe-card-front-creation, .recipe-card-back-creation {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.recipe-card-front-creation {
  background-color: #ffffff;
  background-image: url("https://www.transparenttextures.com/patterns/cream-pixels.png");
  color: black;
  display: grid;
  grid-template-rows: 4.7fr 1fr;
  text-align: center;
  border-radius: 2px;
}

