.image-container-creation {
  margin-top: 6%;
  justify-self: center;
  width: 88%;
}

.image-container-creation img {
  width: 100%;
  /* transform: translate3d(0,0,0); */
  -webkit-backface-visibility: hidden; /*Safari */
  backface-visibility: hidden;
}

.recipe-front-text-creation {
  grid-row-start: 2;
  grid-row-end: 3;
  font-size: 1vw;
  align-self: end;
  margin-bottom: 1.5vh;
}

.recipe-card-back-creation {
  background-color: white;
  color: black;
  transform: rotateY(180deg);
  text-align: center;
  border-radius: 2px;
  display: grid;
  grid-template-rows: 1fr 1fr 4fr;
  overflow: hidden;
}


.recipe-back-title-creation {
  grid-row-start: 1;
  grid-row-end: 2;
  font-size: 1vw;
  overflow: hidden;
}

.recipe-back-description-creation {
  grid-row-start: 3;
  grid-row-end: 4;
  font-size: 1.2vw;
  width: 90%;
  margin: 0px auto;
  text-align: center;
  overflow: hidden;
}

.recipe-back-author-container-creation {
  grid-row-start: 2;
  grid-row-end: 3;
  display: grid;
  grid-template-rows: 1fr 1fr;
  position: relative;
}

.recipe-back-author-creation {
  grid-row-start: 2;
  grid-row-end: 3;
  font-size: 1vw;
}

.recipe-back-author-pic-creation img {
  min-width: 6vh;
  width: 4vw;
  min-height: 6vh;
  height: 4vw;
  border-radius: 50%;
  background-color: grey;
  grid-row-start: 1;
  grid-row-end: 2;
  position: absolute;
  left: 50%;
  bottom: 50%;
  transform: translate(-50%, 50%);
  transition: all 300ms;
  transform-origin: center;
}

.recipe-back-author-pic-creation img:hover {
  width: 10vh;
  height: 10vh;
}

.crop-container {
  max-width: 50%;
  margin: auto;
  /* max-height: 100%; */
}

@media (max-width: 1000px) {
  .recipe-front-text {
    font-size: 1vw;
  }

  .recipe-back-title {
    font-size: .8vw;
  }

  .recipe-back-description {
    font-size: 1.2vw;
  }

  .recipe-back-author {
    font-size: 1.1vw;
  }
}

@media (max-width: 750px) {
  .recipe-front-text {
    font-size: 1.3vw;
  }

  .recipe-back-title {
    font-size: 1.1vw;
  }

  .recipe-back-description {
    font-size: 1.7vw;
  }

  .recipe-back-author {
    font-size: 1.35vw;
  }
}

@media (max-width: 550px) {
  .recipe-front-text {
    font-size: 1.8vw;
  }

  .recipe-back-title {
    font-size: 1.5vw;
  }

  .recipe-back-description {
    font-size: 2.3vw;
  }

  .recipe-back-author {
    font-size: 2vw;
  }
}

@media (max-width: 365px) {
  .recipe-front-text {
    font-size: 3vw;
  }

  .recipe-back-title {
    font-size: 1vh;
  }

  .recipe-back-description {
    font-size: 1.5vh;
  }

  .recipe-back-author {
    font-size: 1.5vh;
  }
}
